import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { Form, Button, Row, Col, Table } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { updateUserProfile, getUserDetails } from '../actions/userActions'
import { listMyOrders } from '../actions/orderActions'
import Loader from '../components/Loader';
import Message from '../components/Message';


const ProfileScreen = () => {

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [message, setMessage] = useState('')

    const dispatch = useDispatch()

    const navigate = useNavigate()

    // Get user data from redux state
    const userDetails= useSelector(state => state.userDetails)
    const { error, loading, user } = userDetails

    // Get user data from redux state
    const userLogin= useSelector(state => state.userLogin)
    const { userInfo } = userLogin
    
    // Get the user updated info from redux
    const userUpdateProfile = useSelector(state => state.userUpdateProfile)
    const { success } = userUpdateProfile

    // Get user orders from redux
    const orderListMy = useSelector(state => state.orderListMy)
    const { loading: loadingOrders, error: errorOrders, orders } = orderListMy


    useEffect(() => {

        if (!userInfo) {

            navigate('/login')

        } else {
            if (!user || !user.name || success || userInfo._id !== user._id) {
                dispatch({
                    type: 'USER_UPDATE_PROFILE_RESET'
                })
                dispatch(getUserDetails('profile'))
                dispatch(listMyOrders())

            } else {
                setName(user.name)
                setEmail(user.email)
            }
        }

    }, [navigate, dispatch, userInfo, user, success])


    const submitHandler = (e) => {
        e.preventDefault()
        
        if (password !== confirmPassword) {
            setMessage('Passwords do not match')   

        } else {
            dispatch(updateUserProfile({
                'id':user._id,
                'name': name,
                'email': email,
                'password': password,
            }))
            setMessage('')
        }
    }

    if (loading) {
        return <Loader/>
    }


    return (
        <Row>
            <Col md={3}>
                <h2>User Profile</h2>

                {message && <Message variant='danger'>{message}</Message>}
                {error && <Message variant='danger'>{error}</Message>}         
                <Form onSubmit={submitHandler}>
                    <Form.Group controlId='name'>
                        <Form.Label>Name</Form.Label>
                        <Form.Control required type='name' placeholder='Enter Name' value={name}
                            onChange={(e) => setName(e.target.value)}/>
                    </Form.Group>

                    <Form.Group controlId='email'>
                        <Form.Label>Email Address</Form.Label>
                        <Form.Control required type='email' placeholder='Enter Email' value={email}
                            onChange={(e) => setEmail(e.target.value)}/>
                    </Form.Group>

                    <Form.Group controlId='password' className='mt-3'>
                        <Form.Label>Password</Form.Label>
                        <Form.Control type='password' placeholder='Enter Password' value={password}
                            onChange={(e) => setPassword(e.target.value)}/>
                    </Form.Group>

                    <Form.Group controlId='password-confirm' className='mt-3'>
                        <Form.Label>Confirm Password</Form.Label>
                        <Form.Control type='password' placeholder='Confirm Password' value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}/>
                    </Form.Group>

                    <Button type='submit' variant='primary mt-4'>Update</Button>
                </Form>
            </Col>

            <Col md={9}>
                <h2>My Orders</h2>
                {loadingOrders ? (<Loader/>)
                    : errorOrders ? (
                        <Message variant='danger'>{errorOrders}</Message>
                    )
                    : (
                        <Table striped responsive className='table-sm'>
                            <thead>
                                <tr>
                                    <th>ID</th>
                                    <th>Date</th>
                                    <th>Total</th>
                                    <th>Paid</th>
                                    <th>Delivered</th>
                                    <th></th>
                                </tr>
                            </thead>

                            <tbody>
                                {orders.map(order => (
                                    <tr key={order._id}>
                                        <td>{order._id}</td>
                                        <td>{order.createdAt.substring(0,10)}</td>
                                        <td>${order.totalPrice}</td>
                                        <td>{order.isPaid ? order.paidAt.substring(0,10) : (
                                                <i className='fas fa-times' style={{color: 'red'}}></i>
                                            )}
                                        </td>
                                        <td>
                                            <LinkContainer to={`/order/${order._id}`}>
                                                <Button className='btn-sm'>Details</Button>
                                            </LinkContainer>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                    )
                }
            </Col>
        </Row>
    )
}

export default ProfileScreen
